}

.vvd:hover {
  background-color: #f7aef8;
}

.vvd span {
  font-weight: bold;
}

/************ Banner Css ************/
.banner {
  margin-top: 0;
  padding: 260px 0 100px 0;
  /*background-image: url('./assets/img/banner-bg.png');*/
  background-image: url('./assets/img/coding_bg.jpg');
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}
.banner .tagline { /*Welcome to my portfolio*/
  font-weight: 700;
  letter-spacing: 0.8px;
  padding: 8px 10px;
  /*background: linear-gradient(90.21deg, rgba(170, 54, 124, 0.5) -5.91%, rgba(74, 47, 189, 0.5) 111.58%);*/
  background: linear-gradient(90.21deg, #f7aef8 -5.91%, rgba(47, 146, 189, 0.5) 111.58%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 20px;
  margin-bottom: 16px;
  display: inline-block;
}
.banner h1 {
  font-size: 65px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1;
  margin-bottom: 20px;
  display: block;
}
.banner p {
  color: #eedaee;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  width: 96%;
}
.banner button {
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  margin-top: 60px;
  letter-spacing: 0.8px;
  display: flex;
  align-items: center;
}
.banner button svg {
  font-size: 25px;
  margin-left: 10px;
  transition: 0.3s ease-in-out;
  line-height: 1;
}
.banner button:hover svg {
  margin-left: 25px;
}
.banner img {
  animation: updown 3s linear infinite;
}
@keyframes updown {
    0% {
        transform: translateY(-25px);
    }
    50% {
        transform: translateY(25px);
    }
    100% {
        transform: translateY(-25px);
    }
}
.txt-rotate > .wrap {
  border-right: 0.08em solid #666;
}

/************ Skills Css ************/
.skill {
  padding: 0 0 50px 0;
  position: relative;
}

.skill-bx {
  background: #151515;
  border-radius: 64px;
  text-align: center;
  padding: 60px 50px;
  margin-top: -60px; 
  border: 2px solid #d291bc;

}
.skill h2 {
	font-size: 45px;
	font-weight: 700;
}
.skill p {
    color: #B8B8B8;
    font-size: 18px;
    letter-spacing: 0.8px;
    line-height: 1.5em;
    margin: 14px 0 75px 0;
}
.skill-slider {
  width: 80%;
  margin: 0 auto;
  position: relative;
}
.skill-slider .item img {
  width: 50%;
  margin: 0 auto 15px auto;
}
.background-image-left {
  top: 28%;
  position: absolute;
  bottom: 0;
  width: 40%;
  z-index: -4;
}

.skill-card {
 /* background: linear-gradient(90.21deg, #f37eaa -5.91%, #d5aadb 111.58%);*/
  background: linear-gradient(90.21deg, #a0eef6 -5.91%, #e5cae9 111.58%);
  color: #151515;
  padding: 30px 15px; /* smaller padding */
  border-radius: 20px;
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: 140px; /* reduced height */
  margin: 10px;  /* adds spacing between cards */
  display: flex;
  align-items: center;
  justify-content: center;
}

.skill-card h5 {
  color: #151515;
  font-size: 16px; /* slightly smaller text */
  font-weight: 600;
  margin: 0;
  text-align: center;
  line-height: 1.4;
}


.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(210, 145, 188, 0.4);
}


/************ Projects Css ************/
.project {
  padding: 80px 0;
  position: relative;
  background-color: black;
}
.project h2 {
	font-size: 45px;
	font-weight: 700;
  text-align: center;
}
.project p {
  color: #B8B8B8;
  font-size: 18px;
  letter-spacing: 0.8px;
  line-height: 1.5em;
  margin: 14px auto 30px auto;
  text-align: center;
  width: 56%;
}
.project .nav.nav-pills {
  width: 72%;
  margin: 0 auto;
  border-radius: 50px;
  background-color: rgb(255 255 255 / 10%);
  overflow: hidden;
}
.project .nav.nav-pills .nav-item {
  width: 33.33333%;
}
.project .nav.nav-pills .nav-link {
  background-color: transparent;
  border-radius: 0;
  padding: 17px 0;
  color: #fff;
  width: 100%;
  font-size: 17px;
  letter-spacing: 0.8px;
  font-weight: 500;
  position: relative;
  transition: 0.3s ease-in-out;
  text-align: center;
  z-index: 0;
}
.project .nav.nav-pills .nav-link::before { /*pill box*/
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    background: linear-gradient(90.21deg, #d291bc -5.91%, #047bc2 111.58%);
    top: 0;
    left: 0;
    z-index: -1;
    transition: 0.3s ease-in-out;
}
.project .nav.nav-pills .nav-link.active::before {
    width: 100% !important;
}
.project .nav.nav-pills .nav-link.active {
    border: 1px solid rgba(255, 255, 255, 1);
}
.nav-link#projects-tabs-tab-first {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 55px 0px 0px 55px;
}
.nav-link#projects-tabs-tab-second {
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.nav-link#projects-tabs-tab-third {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0 55px 55px 0;
}
.proj-imgbx {
  position: relative;
  border-radius: 30px;
  overflow: hidden;
  margin-bottom: 24px;
}
.proj-imgbx::before { /*gradient-box*/
  content: "";
  background: linear-gradient(90.21deg, #047bc2 -5.91%,  #d291bc 111.58%);
  opacity: 0.85;
  position: absolute;
  width: 100%;
  height: 0;
  transition: 0.4s ease-in-out;
}
.proj-imgbx:hover::before {
  height: 100%;
}
.proj-txtx {
  position: absolute;
  text-align: center;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease-in-out;
  opacity: 0;
  width: 100%;
}
.proj-imgbx:hover .proj-txtx {
  top: 50%;
  opacity: 1;
}
.proj-txtx h4 {
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 0.8px;
  line-height: 1.1em;
}
.proj-txtx span {
  font-style: italic;
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0.8px;
}
.background-image-right {
  top: 20%;
  position: absolute;
  bottom: 0;
  width: 35%;
  right: 0;
  z-index: -4;
}


/*********** Work Experience Section **********/

.work-exp-card {
  position: relative;
  padding-left: 30px;
  border-left: 3px solid #00e5ff;
  margin-bottom: 40px;
}

.timeline-dot {
  position: absolute;
  top: 0;
  left: -9px;
  width: 16px;
  height: 16px;
  background-color: #00e5ff;
  border-radius: 50%;
  border: 3px solid #121212;
  box-shadow: 0 0 0 3px #00e5ff;
}


.work-exp-content h4 {
  margin-bottom: 5px;
  font-weight: bold;
  color: #ffffff;
}

.work-exp-content .company {
  font-weight: 600;
  margin-bottom: 3px;
  color: #cccccc;
}

.work-exp-content .duration {
  font-size: 0.9rem;
  color: #999999;
  margin-bottom: 10px;
}

.work-exp-content ul {
  padding-left: 20px;
  margin-bottom: 10px;
}

.work-exp-content li {
  margin-bottom: 6px;
  line-height: 1.5;
  color: #dddddd;
}

.work-exp-content .highlight {
  margin-top: 10px;
  color: #00ffc3;
  font-weight: 500;
}

