/************ Responsive CSS for Mobile ************/
@media (max-width: 768px) {

  nav.navbar {
    padding: 10px 20px;
  }

  nav.navbar a.navbar-brand {
    width: 30%;
  }

  .navbar-text button {
    padding: 10px 18px;
    font-size: 14px;
    margin-left: 10px;
  }

  .banner {
    padding: 180px 20px 60px 20px;
    text-align: center;
  }

  .banner h1 {
    font-size: 34px;
    line-height: 1.3;
  }

  .banner p {
    font-size: 15px;
    width: 100%;
    margin-top: 10px;
  }

  .banner button {
    margin: 40px auto 0;
    font-size: 16px;
    padding: 10px 20px;
  }

  .skill-bx {
    padding: 30px 20px;
    border-radius: 30px;
    margin-top: -30px;
  }

  .skill h2, .project h2 {
    font-size: 30px;
  }

  .skill p, .project p {
    font-size: 15px;
    width: 100%;
    margin: 10px 0 40px 0;
  }

  .project .nav.nav-pills {
    width: 100%;
    flex-direction: column;
    gap: 10px;
  }

  .project .nav.nav-pills .nav-item {
    width: 100%;
  }

  .proj-txtx h4 {
    font-size: 20px;
  }

  .proj-txtx span {
    font-size: 14px;
  }

  .skill-slider {
    width: 100%;
  }

  .skill-card {
    height: auto;
    padding: 16px;
    margin: 8px 4px;
  }

  .skill-card h5 {
    font-size: 14px;
  }

  .work-exp-card {
    padding-left: 15px;
  }

  .timeline-dot {
    width: 12px;
    height: 12px;
    left: -6px;
  }

  .expertise {
    padding: 30px 15px;
  }

  .skills-grid {
    grid-template-columns: 1fr; /* Stack categories vertically */
  }

  /* Contact section mobile fix */
  .contact {
    padding: 40px 20px;
  }

  .contact h2 {
    font-size: 30px;
  }

  .contact form input,
  .contact form textarea {
    font-size: 14px;
    padding: 10px;
  }
}
